html,body
{
    width: 100%;
    height: 100%;
    margin: 0px;
    padding: 0px;
    overflow-x: hidden; 
}

@font-face {
    font-family: 'Zig';
    src: local('Zig'), url(../fonts/zig.ttf) format('truetype');
  }
.title2 {
    color: white;
    text-align: center;
}
section {
    padding:1em;
    padding-bottom: 5em;
    display: flex;
    flex-flow: column;
    align-items: center;
}
.gameMain {
    background-image: 
    linear-gradient(to bottom, rgba(0, 0, 0, 0.52), rgb(173, 255, 47)),
    url('../img/image001_ver3.png');
    background-repeat: no-repeat;
    background-size: 100%;
}
.gameMain h2 {
    font-family: Zig;
    font-size: 4em;
    padding-bottom: 1em;
    padding-top: 0.5em;
}
.containerPadding {
    padding: 0;
}
.gameInfo {
    color: white;
    background-image: 
    linear-gradient(to bottom, rgba(0, 0, 0, 0.52), rgb(255, 255, 255));
}

.gameInfo h2 {
    font-family: Zig;
    font-size: 1.5em;
    padding-top: 0.5em;
    text-align: center;
    color: greenyellow;
}
.gameInfo p {
    text-align:left;
    color: white;
    font-size: 0.6em;
}
.gameInfo img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 50%;
}
.hidden {
    display: none;
}
.gameTableTitle {
    height: 75px;
    font-family: Zig;
    text-align: center;
}
.gameTableTitle button {
    color: greenyellow;
    background: transparent;
}
.gameTableImg {
    width: 100%;
    top: 0;
}
.table>tbody>tr>td, 
.table>tbody>tr>th, 
.table>tfoot>tr>td, 
.table>tfoot>tr>th, 
.table>thead>tr>td, 
.table>thead>tr>th {
      vertical-align: middle;
 }
.tableHeader {
    font-family: Zig;
    text-align: center;
}

.display {
    font-family: Zig;
    display: block;
    text-align: center;
    margin-top: 1em;
}
.displayButton {
    width: 100%;
    font-size: larger;
    background: white;
    color: black;
    display: block;
    padding: 2em;
}
.displayGameText {
    text-align: left;
}
.defaultTexts img {
    width: 90%;
    padding-top: 1em;
}
.filterButtons {
    display: flex;
    flex-direction: column;

    margin-left: 25%;
    text-align: center;
    font-family: Zig;
    width: 100%;
}
.filterButtons button {
    width: 50%;
    color: white;
    background-color: rgba(255,255,255,0.2);
    padding: 0.5em;
}
.filterButtons button:hover {
    background-color: yellowgreen;
}